"use client";
import React, { useMemo, useState } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";
import { Modal } from "@/components/ui/modal";
import Image from "next/image";
import { Eye, Plus, Upload } from "lucide-react";

type ExpenseStatus = "Draft" | "Submitted" | "Approved" | "Rejected";
type TravelType = "Flight" | "Train" | "Cab";

interface TravelExpense {
    id: number;
    tripName: string;
    travelType: TravelType;   // ✅ NEW
    fromLocation: string;
    toLocation: string;
    travelDate: string;
    amount: number;
    status: ExpenseStatus;
    receipt?: string;
}

const initialData: TravelExpense[] = [
    {
        id: 1,
        tripName: "Client Visit",
        travelType: "Flight", // ✅
        fromLocation: "Bangalore",
        toLocation: "Mumbai",
        travelDate: "2025-08-10",
        amount: 4500,
        status: "Submitted",
        receipt: "/images/sample-receipt.png",
    },
    {
        id: 2,
        tripName: "Conference",
        travelType: "Train", // ✅ default
        fromLocation: "Chennai",
        toLocation: "Delhi",
        travelDate: "2025-08-05",
        amount: 9800,
        status: "Approved",
        receipt: "/images/sample-receipt.png",
    },
];

export default function TravelExpensesPage() {
    const [data, setData] = useState<TravelExpense[]>(initialData);
    const [selected, setSelected] = useState<TravelExpense | null>(null);
    const [mode, setMode] = useState<"list" | "create" | "detail">("list");
    const [preview, setPreview] = useState<string | null>(null);

    const [statusFilter, setStatusFilter] = useState<ExpenseStatus | "All">("All");
    const [monthFilter, setMonthFilter] = useState("All");
    const [page, setPage] = useState(1);
    const pageSize = 5;

    const [form, setForm] = useState<Partial<TravelExpense>>({});

    /* ---------------- Filters ---------------- */
    const filtered = useMemo(() => {
        return data.filter((r) => {
            const statusOk = statusFilter === "All" || r.status === statusFilter;
            const monthOk =
                monthFilter === "All" ||
                r.travelDate.startsWith(monthFilter);
            return statusOk && monthOk;
        });
    }, [data, statusFilter, monthFilter]);

    const totalPages = Math.ceil(filtered.length / pageSize);
    const paginated = filtered.slice(
        (page - 1) * pageSize,
        page * pageSize
    );

    /* ---------------- Actions ---------------- */
    const openCreate = () => {
        setForm({
            tripName: "",
            travelType: "Flight", // ✅ default
            fromLocation: "",
            toLocation: "",
            travelDate: "",
            amount: 0,
            status: "Draft",
        });
        setMode("create");
    };

    const openDetail = (r: TravelExpense) => {
        setSelected(r);
        setForm(r);
        setMode("detail");
    };

    const saveDraft = () => {
        const newItem: TravelExpense = {
            ...(form as TravelExpense),
            id: data.length + 1,
            status: "Draft",
        };
        setData([newItem, ...data]);
        setMode("list");
    };

    const submitExpense = () => {
        setData(
            data.map((r) =>
                r.id === selected?.id ? { ...r, status: "Submitted" } : r
            )
        );
        setMode("list");
    };

    /* ---------------- UI ---------------- */
    return (
        <div className="p-6">
            <PageBreadcrumb pageTitle="Travel Expenses" />

            {mode === "list" && (
                <>
                    {/* Header */}
                    <div className="flex justify-between mb-4">
                        <div className="flex gap-2">
                            <select
                                className="border rounded px-3 py-2"
                                onChange={(e) => setStatusFilter(e.target.value as any)}
                            >
                                <option value="All">All Status</option>
                                <option value="Draft">Draft</option>
                                <option value="Submitted">Submitted</option>
                                <option value="Approved">Approved</option>
                                <option value="Rejected">Rejected</option>
                            </select>

                            <input
                                type="month"
                                className="border rounded px-3 py-2"
                                onChange={(e) => setMonthFilter(e.target.value || "All")}
                            />
                        </div>

                        <button
                            onClick={openCreate}
                            className="bg-blue-600 text-white px-4 py-2 rounded flex items-center gap-2"
                        >
                            <Plus size={16} /> New Travel Expense
                        </button>
                    </div>

                    {/* Table */}
                    <div className="overflow-hidden rounded-xl border bg-white">
                        <div className="min-w-[1000px]">
                            <Table>
                                <TableHeader className="border-b">
                                    <TableRow>
                                        <TableCell isHeader className="px-5 py-3 text-left">
                                            Trip
                                        </TableCell>
                                        <TableCell isHeader className="px-5 py-3 text-left">
                                            Type
                                        </TableCell>
                                        <TableCell isHeader className="px-5 py-3 text-left">
                                            Route
                                        </TableCell>
                                        <TableCell isHeader className="px-5 py-3 text-left">
                                            Date
                                        </TableCell>
                                        <TableCell isHeader className="px-5 py-3 text-left">
                                            Amount
                                        </TableCell>
                                        <TableCell isHeader className="px-5 py-3 text-left">
                                            Status
                                        </TableCell>
                                        <TableCell isHeader className="px-5 py-3 text-left">
                                            Action
                                        </TableCell>
                                    </TableRow>
                                </TableHeader>

                                <TableBody className="divide-y">
                                    {paginated.map((r) => (
                                        <TableRow key={r.id}>
                                            <TableCell className="px-5 py-4 font-medium">
                                                {r.tripName}
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                {r.travelType}
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                {r.fromLocation} → {r.toLocation}
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                {r.travelDate}
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                ₹{r.amount}
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                <Badge
                                                    size="sm"
                                                    color={
                                                        r.status === "Approved"
                                                            ? "success"
                                                            : r.status === "Submitted"
                                                                ? "warning"
                                                                : r.status === "Rejected"
                                                                    ? "error"
                                                                    : "secondary"
                                                    }
                                                >
                                                    {r.status}
                                                </Badge>
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                <button
                                                    onClick={() => openDetail(r)}
                                                    className="text-blue-600 hover:underline flex items-center gap-1"
                                                >
                                                    <Eye size={14} /> View
                                                </button>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </div>
                    </div>

                    {/* Pagination */}
                    {totalPages > 1 && (
                        <div className="mt-4 flex justify-center gap-2">
                            {Array.from({ length: totalPages }).map((_, i) => (
                                <button
                                    key={i}
                                    onClick={() => setPage(i + 1)}
                                    className={`px-3 py-1 rounded border ${page === i + 1
                                            ? "bg-blue-600 text-white"
                                            : "bg-white"
                                        }`}
                                >
                                    {i + 1}
                                </button>
                            ))}
                        </div>
                    )}
                </>
            )}

            {/* Create / Detail */}
            {(mode === "create" || mode === "detail") && (
                <div className="max-w-xl mx-auto bg-white p-6 rounded-xl shadow">
                    <h2 className="text-lg font-semibold mb-4">
                        {mode === "create" ? "New Travel Expense" : "Travel Expense"}
                    </h2>

                    <div className="space-y-3">
                        <input
                            placeholder="Trip Name"
                            className="w-full border rounded px-3 py-2"
                            value={form.tripName || ""}
                            onChange={(e) =>
                                setForm({ ...form, tripName: e.target.value })
                            }
                            disabled={mode === "detail"}
                        />

                        <select
                            value={form.travelType || "Flight"}
                            onChange={(e) =>
                                setForm({ ...form, travelType: e.target.value as TravelType })
                            }
                            disabled={mode === "detail"}
                        >
                            <option value="Flight">Flight</option>
                            <option value="Train">Train</option>
                            <option value="Cab">Cab</option>
                        </select>

                        <input
                            placeholder="From"
                            className="w-full border rounded px-3 py-2"
                            value={form.fromLocation || ""}
                            onChange={(e) =>
                                setForm({ ...form, fromLocation: e.target.value })
                            }
                            disabled={mode === "detail"}
                        />

                        <input
                            placeholder="To"
                            className="w-full border rounded px-3 py-2"
                            value={form.toLocation || ""}
                            onChange={(e) =>
                                setForm({ ...form, toLocation: e.target.value })
                            }
                            disabled={mode === "detail"}
                        />

                        <input
                            type="date"
                            className="w-full border rounded px-3 py-2"
                            value={form.travelDate || ""}
                            onChange={(e) =>
                                setForm({ ...form, travelDate: e.target.value })
                            }
                            disabled={mode === "detail"}
                        />

                        <input
                            type="number"
                            placeholder="Amount"
                            className="w-full border rounded px-3 py-2"
                            value={form.amount || 0}
                            onChange={(e) =>
                                setForm({ ...form, amount: Number(e.target.value) })
                            }
                            disabled={mode === "detail"}
                        />

                        {form.receipt && (
                            <button
                                onClick={() => setPreview(form.receipt!)}
                                className="text-blue-600 underline text-sm"
                            >
                                Preview Receipt
                            </button>
                        )}

                        {mode === "create" && (
                            <label className="flex items-center gap-2 cursor-pointer">
                                <Upload size={16} />
                                <span>Upload Receipt</span>
                                <input
                                    type="file"
                                    hidden
                                    onChange={(e) =>
                                        setForm({
                                            ...form,
                                            receipt: URL.createObjectURL(e.target.files![0]),
                                        })
                                    }
                                />
                            </label>
                        )}

                        <div className="flex justify-end gap-2 mt-4">
                            <button
                                className="px-4 py-2 border rounded"
                                onClick={() => setMode("list")}
                            >
                                Back
                            </button>

                            {mode === "create" && (
                                <button
                                    onClick={saveDraft}
                                    className="px-4 py-2 bg-blue-600 text-white rounded"
                                >
                                    Save Draft
                                </button>
                            )}

                            {mode === "detail" && selected?.status === "Draft" && (
                                <button
                                    onClick={submitExpense}
                                    className="px-4 py-2 bg-green-600 text-white rounded"
                                >
                                    Submit
                                </button>
                            )}
                        </div>
                    </div>
                </div>
            )}

            {/* Receipt Preview */}
            <Modal
                isOpen={!!preview}
                onClose={() => setPreview(null)}
                className="max-w-lg p-4"
            >
                {preview && (
                    <Image
                        src={preview}
                        alt="Receipt"
                        width={500}
                        height={500}
                        className="rounded"
                    />
                )}
            </Modal>
        </div>
    );
}
